import React, { Component } from 'react';
import { Accordion, Offcanvas } from 'react-bootstrap';
import { connect } from 'react-redux';


class FilterAside extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        isActiveAside: false,
        feedData: []
    };
    onLoadFeedList = () => {
        const { filterList } = this.props.feedData;
        this.setState((prevState) => ({ ...prevState, feedData: filterList }));
    }

    componentDidMount() {
        var filterAside = document.getElementById("filter_aside");
        if (filterAside) {
            filterAside.addEventListener("click", this.toogleFilter);
        }
        this.onLoadFeedList()
    }
    toogleFilter = () => {
        this.setState({
            isActiveAside: !this.state.isActiveAside,
        })
        setTimeout(() => {
            var offcanvas = document.querySelector(".offcanvas-backdrop");
            if (offcanvas) {
                offcanvas.classList.add('filter-backdrop');
            }
        }, 100);
    };


    render() {
        const { isActiveAside, feedData } = this.state
        const { onfeedNetworkfilterApply, ontypeFilterApply, onlocationfilterApply, onOtherFilterApply, onUgcrightsFilterApply, feedNetworkfilter, typeFilter, locationfilter, otherFilter, ugcrightsFilter } = this.props

        return <Offcanvas show={isActiveAside} placement={'end'} name={'end'} onHide={this.toogleFilter} className="filter_aside">
            <Offcanvas.Body>
                <div className="d-flex align-items-center justify-content-between px-3 px-xxl-4 py-3 border-bottom">
                    <div className="fs-5 fw-medium">Add Filter</div>
                    <button type="button" className="btn btn-icon btn-active-light btn-active-color-primary p-0 w-30px h-30px border-0" onClick={this.toogleFilter}>
                        <span className="svg-icon svg-icon-5 me-0">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                <path fill="none" d="M0 0h24v24H0z" />
                                <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" fill="currentColor" />
                            </svg>
                        </span>
                    </button>
                </div>
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-7 fw-medium">Social Network</span></Accordion.Header>
                        <Accordion.Body className="pt-1">
                            {
                                feedData && Object.keys(feedData).length > 0 && feedData.feeds.length > 0 ? feedData.feeds.map((item) => {
                                    return item.name ? <div key={item} className="d-flex align-items-start justify-content-between">
                                        <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                            <input className="form-check-input" type="checkbox" name={item.feedId} value={item.feedId} />
                                            <span className="form-check-label">{item.name}</span>
                                        </label>
                                        <span className="badge text-muted border rounded-4 mt-1 min-w-30px">{item.count}</span>
                                    </div> : null
                                }) : null
                            }
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-7 fw-medium">Post Type</span></Accordion.Header>
                        <Accordion.Body className="pt-1">
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" id="image_type" onClick={ontypeFilterApply("2,4")} checked={Object.hasOwnProperty("2,4")} />
                                    <span className="form-check-label">Image Only</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" id="video_type" onClick={ontypeFilterApply("3,5")} checked={Object.keys(typeFilter).includes("3,5") ? true : false} />
                                    <span className="form-check-label">Video Only</span>
                                </label>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-7 fw-medium">Location</span></Accordion.Header>
                        <Accordion.Body className="pt-1">
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Dubai</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Queensland, Australia</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Rajasthan, India</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Hong kong, China</span>
                                </label>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-7 fw-medium">Other</span></Accordion.Header>
                        <Accordion.Body className="pt-1">
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Highlight Post</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Pinned to top</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Recent</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Retweets</span>
                                </label>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Accordion flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-7 fw-medium">UGC Rights</span></Accordion.Header>
                        <Accordion.Body className="pt-1">
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Pending Post</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Approved Post</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Rejected Post</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Expired Post</span>
                                </label>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <div className="d-flex justify-content-center text-align-center flex-column w-100 px-4 pb-4">
                    <button type="button" className="btn btn-primary w-100 mb-1">Filter</button>
                    <button type="button" className="btn border-0 text-hover-primary btn-sm text-decoration-underline text-muted p-1">Reset results</button>
                </div>
            </Offcanvas.Body>
        </Offcanvas>
    }
}

const mapStateToProps = state => {
    return {
        feedData: state.feedData
    }
}

export default connect(mapStateToProps)(FilterAside);